.card {
  -webkit-animation: fadein 2s;
          animation: fadein 2s;
  border: 1px solid #330;
  border-radius: 4px;
  -webkit-box-shadow: 2px 2px 5px 3px #eee;
          box-shadow: 2px 2px 5px 3px #eee;
  background-color: #fff;
  max-width: 640px;
  min-width: 320px;
  padding: 0;
}



